import React, { lazy} from 'react';
// requests
import FetchSessionsByDevice from '../js/request/get-sessions-by-device'
import FetchAudienceOverview from '../js/request/get-audience-overview'
import FetchSessions from '../js/request/get-sessions'
import FetchSessionDuration from '../js/request/get-session-duration'
import FetchGeoNetwork from '../js/request/get-geo-network'
import FetchMapboxToken from '../js/request/get-mapbox-token'
// material-ui
import PropTypes from 'prop-types';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
// Components
import ResponsiveGraphs from './responsive-graphs/responsive-graphs';
import MapsCharts from './responsive-graphs/maps-charts';
// Airbnb - react-dates
import { DateRangePicker } from 'react-dates';
// metrics
const SessionsByDevicePie = lazy(() => import('./metrics/sessions-By-Device-Pie-Chart'));
const AudienceOverviewResponsiveLine = lazy(() => import('./metrics/audience-Overview-Responsive-Line'));
const SessionsResponsiveLine = lazy(() => import('./metrics/sessions-Responsive-Line'));
const SessionDurationResponsiveLine = lazy(() => import('./metrics/session-Duration-Responsive-Line'));
const GeoNetworkChoropleth = lazy(() => import('./metrics/geo-network-choropleth'));
const MapBox = lazy(() => import('./mapbox-testing/map-box'));

export {
    // metrics
    SessionsByDevicePie,
    AudienceOverviewResponsiveLine,
    SessionsResponsiveLine,
    SessionDurationResponsiveLine,
    GeoNetworkChoropleth,
    MapBox,
    // requests
    FetchSessionsByDevice,
    FetchAudienceOverview,
    FetchSessions,
    FetchSessionDuration,
    FetchGeoNetwork,
    FetchMapboxToken,
    // material-ui
    PropTypes,
    SwipeableViews,
    makeStyles,
    useTheme,
    Paper,
    Tabs,
    Tab,
    Box,
    Grid,
    // Components
    ResponsiveGraphs,
    MapsCharts,
    // Airbnb - react-dates
    DateRangePicker
}